import React, {Component} from 'react';
import store from '../store';
import {connect} from 'react-redux';
import Board from '../components/Board';
import io from 'socket.io-client';
import {getBoardNotes, getBoard} from '../actions/board';
import {getAllNotes} from '../actions/notes';

// const socket = io('', { path: '/api/board/1' });
function onBoardEnter(nextRouterState) {
  const boardId = nextRouterState.params.boardId;
  store.dispatch(getBoard(boardId));
  store.dispatch(getAllNotes({boardId}));
}

class BoardContainer extends Component {

  componentDidMount() {
    onBoardEnter();
    const { dispatch, board} = this.props;

    const boardId = board.id;

    // dispatch(getBoardNotes(boardId));

  }

  render() {
    return (
      <Board {...this.props} />
    );
  }
}


const mapStateToProps = (state) => ({board: state.board.selectedBoard});

const mapDispatchToProps = (dispatch) => ({});

export default connect(mapStateToProps, mapDispatchToProps)(BoardContainer);



